import React, { Component, Fragment } from 'react';
import axios from 'axios';
import { confirmAlert } from 'react-confirm-alert'; // Import
import 'react-confirm-alert/src/react-confirm-alert.css';
import swal from 'sweetalert';

class Manage6 extends Component {
  constructor(props) {
    super(props);

    this.state = {
      requestsid: '',
      answer: '',
    };
    this.handleChange = this.handleChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  handleChange(e) {
    this.setState({ [e.target.id]: e.target.value });
  }

  handleSubmit(e) {
    e.preventDefault();

    confirmAlert({
      title: 'Confirm',
      message: ' ?',
      buttons: [
        {
          label: '',
          onClick: () => {
            console.log(this.state.requestsid);

            axios({
              method: 'put',
              url: `/api/v1/requests/${this.state.requestsid}`,
              data: { answer: `${this.state.answer}` },
            })
              .then((res) => {
                if (res.status === 200) {
                  swal('');
                }
              })
              .catch(function (error) {
                if (error.response.status === 403) {
                  swal(' ');
                } else if (error.response) {
                  swal(' id ');
                }
              });
            this.setState({
              requestsid: '',
              answer: '',
            });
          },
        },
        {
          label: '',
          onClick: () => {
            return;
          },
        },
      ],
    });
  }
  render() {
    return (
      <Fragment>
        <form className="managebox6">
          <input
            placeholder=" id "
            value={this.state.requestsid}
            onChange={this.handleChange}
            id="requestsid"
          ></input>
          <textarea
            placeholder=""
            value={this.state.answer}
            onChange={this.handleChange}
            id="answer"
          ></textarea>
          <button onClick={this.handleSubmit}> </button>
        </form>
      </Fragment>
    );
  }
}

export default Manage6;
